@import "../fonts/style.css";
@import "./reset.css";
@import "./body.css";

:root {
  --rsbs-backdrop-bg: rgba(0, 0, 0, 0.6);
  /* --rsbs-bg: #f4f4f2; */
  --rsbs-bg: white;
  --rsbs-handle-bg: hsla(0, 0%, 0%, 0.14);
  --rsbs-max-w: auto;
  --rsbs-ml: env(safe-area-inset-left);
  --rsbs-mr: env(safe-area-inset-right);
  --rsbs-overlay-rounded: 16px;

  --rsbs-max-w: 768px;
  --rsbs-ml: auto;
  --rsbs-mr: auto;
}